@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	font-family: "Nunito Sans", sans-serif;
}

:root {
	--Mode-Elements: hsl(209, 23%, 22%);
	--Mode-Background: hsl(207, 26%, 17%);
	--Mode-Text: hsl(0, 0%, 100%);
	--box-shadow: black;
}
body {
	background-color: hsl(207, 26%, 17%);
	background-color: var(--Mode-Background);
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
	max-width: 100%;
	max-width: 100vw;
	overflow-x: hidden;
}

/* to be able to evaluate the present theme */
.theme-toggle span,
.far {
	cursor: pointer;
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
}
.pre-animation {
	opacity: 0.5;
	transition: opacity ease-out 0.5s;
}
.post-animation {
	transition: opacity ease-in 0.5s;
}

.header {
	height: 3em;
	padding: 1em;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	box-shadow: 0px 0px 1px black;
	box-shadow: 0px 0px 1px var(--box-shadow);
}
.header p,
h1 {
	display: inline-block;
	cursor: default;
}

h1 {
	font-size: 1em;
	width: 60%;
}

.header p {
	width: 40%;
	font-size: 0.8em;
	text-align: right;
}

.input-div {
	display: inline-block;
	width: 90%;
	height: 3em;
	margin: 3% 5% 0;
	padding-left: 10%;
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	box-shadow: 0px 0px 2px black;
	box-shadow: 0px 0px 2px var(--box-shadow);
}
.input-div img {
	display: inline-block;
	margin-right: 3%;
	width: 7%;
	position: relative;
	top: 0.5em;
}

input,
select {
	display: inline-block;
	font-size: 1em;
	font-weight: 600;
}
input {
	cursor: pointer;
	width: 90%;
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	height: 95%;
	border: none;
	border-radius: 5px;
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
	text-indent: 5%;
}

::-webkit-input-placeholder {
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
	opacity: 0.7;
}

:-ms-input-placeholder {
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
	opacity: 0.7;
}

::placeholder {
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
	opacity: 0.7;
}

input:focus {
	box-shadow: 0px 0px 2px black;
	box-shadow: 0px 0px 2px var(--box-shadow);
	outline: none;
	border: 2px solid hsl(0, 0%, 100%);
	border: 2px solid var(--Mode-Text);
}

select {
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	box-shadow: 0px 0px 2px black;
	box-shadow: 0px 0px 2px var(--box-shadow);
	border: none;
	height: 2.5em;
	padding: 0.5em;
	width: 50%;
	margin: 5%;
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
}

option {
	font-weight: 600;
	height: 2em;
}

select:focus {
	outline: none;
}

.countries-list {
	margin: 5% 15%;
}
.country-detail {
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	height: 20em;
	box-shadow: 0px 0px 2px black;
	box-shadow: 0px 0px 2px var(--box-shadow);
	margin-bottom: 15%;
}

.country-detail a {
	cursor: pointer;
	text-decoration: none;
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
	height: 100%;
	display: grid;
	grid-template-rows: 50% 50%;
}
.country-detail img {
	width: 100%;
	height: 10em;
	object-fit: cover;
}

.detail {
	padding: 7%;
}

p {
	font-weight: 600;
}
.tag {
	font-weight: 300;
}

.detail h2 {
	margin-bottom: 5%;
	font-size: 1.3em;
}

a {
	text-decoration: none;
	color: hsl(0, 0%, 100%);
	color: var(--Mode-Text);
}
.home {
	font-size: 0.8em;
	max-width: 20%;
	height: 2em;
	padding: 0.4em;
	padding-right: 0.6em;
	text-align: right;
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	margin: 5%;
	box-shadow: 0px 0px 2px black;
	box-shadow: 0px 0px 2px var(--box-shadow);
}

.more-detail {
	margin: 15% 7%;
}

.more-detail img {
	width: 100%;
	height: 10em;
	object-fit: cover;
}

.more-detail h2 {
	margin: 7% 0 7%;
	font-size: 1.2em;
}

.more-detail div p {
	margin: 3% 0 3%;
}

.two,
.three {
	margin-top: 15%;
}

.three p {
	font-weight: 300;
	font-size: 0.8em;
}
.three a {
	cursor: pointer;
	margin: 2%;
	display: inline-block;
	background-color: hsl(209, 23%, 22%);
	background-color: var(--Mode-Elements);
	box-shadow: 0px 0px 2px black;
	box-shadow: 0px 0px 2px var(--box-shadow);
	min-width: 28%;
	height: 1.7em;
	padding: 0.2em;
	text-align: center;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

@media screen and (min-width: 375px) {
	.specific {
		padding: 0% 15%;
	}
	.countries-list {
		margin: 5% 25%;
	}
}

@media screen and (min-width: 600px) {
	.header {
		height: 5em;
		padding: 1.5em;
		background-color: hsl(209, 23%, 22%);
		background-color: var(--Mode-Elements);
		box-shadow: 0px 0px 1px black;
		box-shadow: 0px 0px 1px var(--box-shadow);
	}
	.search-filter {
		margin-top: 5%;
		padding: 0 10%;
	}
	.input-div {
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		padding-left: 5%;
	}
	.input-div img {
		top: 0.7em;
	}
	input {
		height: 3em;
		position: relative;
		top: -10%;
	}
	select {
		height: 3em;
		margin: 2% 25%;
	}

	.country-detail {
		height: 25em;
	}
	.country-detail img {
		height: 14em;
	}
	.detail {
		margin-top: 7%;
	}

	.home {
		font-size: 0.8em;
		max-width: 15%;
		height: 2em;
		padding: 0.4em;
		padding-right: 0.6em;
		margin: 2%;
	}
	.specific {
		padding: 0% 10%;
	}
	.countries-list {
		margin: 5%;
		display: grid;
		grid-template-columns: 47% 47%;
		grid-column-gap: 5%;
		-webkit-column-gap: 5%;
		        column-gap: 5%;
	}

	.more-detail {
		margin-top: 5%;
		display: grid;
		grid-template-columns: auto auto;
		grid-column-gap: 3%;
		-webkit-column-gap: 3%;
		        column-gap: 3%;
	}

	.more-detail img {
		width: 80%;
		height: 15em;
		padding-left: 10%;
	}

	.more-detail img,
	.three {
		grid-column: 1 / span 2;
	}
	.three {
		margin-top: 5%;
	}
	h4 {
		display: inline-block;
	}
}

@media screen and (min-width: 992px) {
	h1 {
		font-size: 1.5em;
		padding-left: 5%;
	}
	.header p {
		font-size: 1.2em;
	}
	.search-filter {
		margin-top: 5%;
		padding: 0 5%;
	}

	.input-div {
		width: 45%;
		margin: 0;
	}
	input {
		height: 4em;
		margin: 0;
	}
	select {
		width: 20%;
		height: 4em;
		margin: 0;
		margin-left: 30%;
	}
	.countries-list {
		margin: 5%;
		display: grid;
		grid-template-columns: 30% 30% 30%;
		grid-column-gap: 5%;
		-webkit-column-gap: 5%;
		        column-gap: 5%;
	}

	.home {
		max-width: 10%;
		height: 2.5em;
	}

	.more-detail {
		grid-template-columns: 60% 40%;
		grid-row-gap: 5%;
		row-gap: 5%;
	}

	.more-detail img {
		grid-column: 1 / span 1;
		padding: 0;
	}
	.two {
		margin-top: 0;
	}
	.three {
		grid-column: 2 / span 1;
	}
}

@media screen and (min-width: 1440px) {
	body {
		margin: 0 auto;
		max-width: 1440px;
	}
	.countries-list {
		grid-template-columns: 22% 22% 22% 22%;
		grid-column-gap: 4%;
		-webkit-column-gap: 4%;
		        column-gap: 4%;
	}
	.home {
		width: 8%;
		font-size: 1.2em;
		height: 2.5em;
		padding: 0.6em;
		margin-left: 5%;
	}

	.more-detail h2 {
		font-size: 1.7em;
	}
	.more-detail {
		margin: 5%;
		grid-template-columns: 40% 30% 30%;
		grid-template-rows: auto auto;
		grid-row-gap: 5%;
		row-gap: 5%;
	}
	.specific {
		padding: 0;
	}

	.more-detail img {
		width: 100%;
		height: 20em;
		grid-row: 1 / span 2;
		padding: 0;
	}

	.two {
		margin-top: 20%;
	}
	.three {
		margin-top: 0;
		grid-column: 2 / span 2;
	}

	.three a {
		min-width: 15%;
		height: 2.5em;
		padding: 0.7em;
	}
}

